body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    background: linear-gradient(rgba(0,0,0,0.1),rgba(0,0,0,0.1)),url(/static/media/underwater.488c2090.jpg);
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}

body::-webkit-scrollbar {
    width: 13px;
}

body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);

}

body::-webkit-scrollbar-thumb {
  background: rgb(54,217,182);
  background: linear-gradient(90deg, rgba(54,217,182,1) 0%, rgba(32,152,126,1) 43%, rgba(0,212,255,1) 100%);
  border-radius: 100px;
}


h1, h2, h3, h4, h5, p, span, strike{
  font-family: 'Montserrat', sans-serif;

}

#task-container{
  max-width:600px;
  margin:0 auto;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  background-color: #fff;
  
  margin-top:100px;
  margin-bottom:100px;

  justify-content: space-around;
  align-items: flex-start;

}

#form-wrapper{
  position: -webkit-sticky;
  position: sticky;
  top: 0rem;
  border-bottom: 1px solid  #e9e9e9;
  background-color: #fff;
  box-shadow: 0 3px 8px rgba(0,0,0,0.25);
  padding:40px;
}

#submit{
  background-color: #36d9b6;
  border-radius: 0;
  border:0;
  color: #fff;
}

.flex-wrapper{
  display: flex;
}

.task-wrapper{
  cursor: pointer;
  border-bottom: 1px solid  #e9e9e9;
  color: #686868;
  margin:5px;
  padding:20px;
}

.task-wrapper:hover{
  background-color: #F0F0F0;
}

#list-wrapper{
  max-height: 50vh;
  overflow-y: scroll;
}

#list-wrapper::-webkit-scrollbar {
  width: 0px;
}


/*the given css for bubbles*/
#bubbles img{
  width: 50px;
  -webkit-animation: bubble 7s linear infinite;
          animation: bubble 7s linear infinite;
}

#bubbles{
  width: 100%;
  display:  flex;
  align-items: center;
  justify-content: space-around;
  position: absolute;
  bottom: 0;
}

@-webkit-keyframes bubble{
  0%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 0;
  }
  50%{
    opacity: 1;
  }
  70%{
    opacity: 1;
  }
  100%{
    -webkit-transform: translateY(-80vh);
            transform: translateY(-80vh);
    opacity: 0;
  }
}

@keyframes bubble{
  0%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 0;
  }
  50%{
    opacity: 1;
  }
  70%{
    opacity: 1;
  }
  100%{
    -webkit-transform: translateY(-80vh);
            transform: translateY(-80vh);
    opacity: 0;
  }
}

/* adding time delay to each child */
#bubbles img:nth-child(1){
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  width: 25px;
}
#bubbles img:nth-child(2){
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
#bubbles img:nth-child(3){
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  width: 25px;
}
#bubbles img:nth-child(4){
  -webkit-animation-delay: 4.5s;
          animation-delay: 4.5s;
}
#bubbles img:nth-child(5){
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
#bubbles img:nth-child(6){
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
  width: 20px;
}
#bubbles img:nth-child(17){
  -webkit-animation-delay: 7s;
          animation-delay: 7s;
  width: 35px;
}
